import {iosVhFix} from './utils/ios-vh-fix';
import {initModals} from './modules/modals/init-modals';
import {initAccordions} from './modules/init-accordion';
import {initTabs} from './modules/init-tabs';
// ---------------------------------

window.addEventListener('DOMContentLoaded', () => {

  // Utils
  // ---------------------------------

  iosVhFix();

  // Modules
  // ---------------------------------

  // все скрипты должны быть в обработчике 'DOMContentLoaded', но не все в 'load'
  // в load следует добавить скрипты, не участвующие в работе первого экрана
  window.addEventListener('load', () => {
    // initModals();
    initAccordions();
    initTabs();

    let titleSlider = document.querySelector('.title__slider');
    if(titleSlider) {
      let swiper = new Swiper(titleSlider, {
        slidePerView: 1,
        loop: true,
        pagination: {
          el: '.title__slider-nav',
          clickable: true,
        }
      })
    }

    let goodSliders = document.querySelectorAll('.good__slider');
    if(goodSliders) {
      goodSliders.forEach(function(slider) {
        let swiper = new Swiper(slider, {
          navigation: {
            prevEl:  slider.closest('.good__slider-wrapper').querySelector('.good__slider-btn.--prev'),
            nextEl:  slider.closest('.good__slider-wrapper').querySelector('.good__slider-btn.--next')
          },
          breakpoints: {
            0: {
              slidesPerView: 1,
              spaceBetween: 20,
            },
            768: {
              slidesPerView: 2,
              spaceBetween: 20,
            },
            1220: {
              slidesPerView: 2,
              spaceBetween: 90,
            }
          }
        });
      });
      
    }


    var tl = gsap.timeline({repeat: 0, repeatDelay: 0});
    tl.to('.--cloud-1', {
      x: '-100%',
    });
    ScrollTrigger.create({
      trigger: '.--cloud-1',
      start: 'top 90%',
      endTrigger: '.--last',
      end: 'bottom 100%',
      animation: tl,
      scrub: true
    });

    var tl2 = gsap.timeline({repeat: 0, repeatDelay: 0});
    tl2.to('.--cloud-2', {
      x: '100%',
    });
    ScrollTrigger.create({
      trigger: '.--cloud-2',
      start: 'top 90%',
      endTrigger: '.--last',
      end: 'bottom 100%',
      animation: tl2,
      scrub: true
    });

    var tl3 = gsap.timeline({repeat: 0, repeatDelay: 0});
    tl3.to('.--cloud-3', {
      x: '-100%',
    });
    ScrollTrigger.create({
      trigger: '.--cloud-3',
      start: 'top 90%',
      endTrigger: '.--last',
      end: 'bottom 100%',
      animation: tl3,
      scrub: true
    });

    var tl4 = gsap.timeline({repeat: 0, repeatDelay: 0});
    tl4.to('.--cloud-4', {
      x: '100%',
    });
    ScrollTrigger.create({
      trigger: '.--cloud-4',
      start: 'top 90%',
      endTrigger: '.--last',
      end: 'bottom 100%',
      animation: tl4,
      scrub: true
    });

  });
});

